 
 import React from 'react'
import { useNavigate } from 'react-router-dom'
 import s from '../scss/footer.module.scss'
 export const Footer = () => {
    const history = useNavigate()


  return (
      <div className={s.main}>
          <div onClick={()=>{history('/confidentiality')

             }} className={s.confidence} title='     '><span> </span></div>
          <div onClick={()=>{history('/contacts')}} className={s.contacts}><span></span></div>
          <div onClick={()=>{history('/errors')}} title="   ?" className={s.anyMessage}></div>
    </div>
)
}
 